@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #242629;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#home-box {
    display: flex;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
   
}
.home-about {

    align-self: flex-end;

}

.about {
   margin: 25px 35px 0px 0px;
}

.home-title {
display: flex;
margin: auto;
flex-direction: column;

}
.home-info{
    display: flex;
    justify-content: flex-start;
    
}

h1.javascript {
    font-family: 'Permanent Marker', cursive;
    line-height: 0px;
    font-size: 44px;
    color: #f3ec78;
    margin: 0px 0px -5px 10px;
    padding: 0;
}


h1.gradient-text {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 110px;
   
   
}



.gradient-text {
    background-image: linear-gradient(45deg, #f3ec78, white);
    background-size: 100%;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
}

.home-title h2 {
    font-family: 'Permanent Marker', cursive;
    line-height: 20px;
    font-size: 30px;
}

.video {
    height:100%;
    width: 100%;
    -webkit-filter:blur(5px);
    filter:blur(5px);
    position: absolute;
    top: 0;
    z-index: -1;
    object-fit: cover;
    opacity: 0.4;
 
  }

.home-title h2 {
    font-family: 'Poppins', sans-serif;
    font-size: 1.5em;
    font-weight: 300;
    color: white;
    margin-left: 10px;
    transform: translateY(40px);
}
  

#header-box { 
    background-color: #242629;
      display: flex;
      height: 60px;
      flex-direction: row;
      color: white;
    }
  
    .header-items {
        font-size: 12px;
      display:flex;
     width: 100%;
      justify-content: flex-end;
      margin: auto;
      text-align: right; 
     padding-right: 20px;;
      color: white;
  }

  .about {
      background-image: url(/game-collection/static/media/about.f41b5c23.svg);
      background-size: 100%;
      width: 25px;
      height: 25px;
      margin:0px;
      cursor: pointer;
  }


  .about:hover {
    -webkit-filter: drop-shadow(0px 0px 5px white);
            filter: drop-shadow(0px 0px 5px white);
  }
.btn {
    
    position: relative;
    display: inline-block;
    margin:10px 50px 0px 0px;
    cursor: pointer;
}

.btn:last-child {
    
  margin:10px 0px 0px 0px;
}
.btn img:active {
    transform: scale(.85);
    transition: transform 0.15s ease-in-out;
    cursor: zoom-in;
    
  }

  .btn:hover {
    -webkit-filter: drop-shadow(0px 0px 8px white);
            filter: drop-shadow(0px 0px 8px white);
  }





#game-buttons-box {
    display: flex;
    flex-direction: row;
    background-color: #242629;
    justify-content: center;
  
}

.game-items {
    display:flex;
 justify-items: center;
}
#footer-box { 
  background-color: #242629;
    display: flex;
    height: 50px;
    flex-direction: row;
    color: white;
  }

  .footer-items {
      font-size: 12px;
    display:flex;
    width: 70%;
    justify-content: center;
    margin: auto;
    text-align: center;
    flex-direction: column; 
}
